<script>
  import {GAME} from '../../games'
  import {mapGetters, mapActions} from 'vuex'
  import axios from 'axios'
  import moment from 'moment'
  import {GROUP_SUGGEST_URL, PLACE_BET_URL, MAN_TO_MAN} from '../../api_routes'
  import {AUTH_CONFIG} from '../../auth_config'
  import BetForm from '../Utilities/BetForm'
  import LoadingIndicator from "../Utilities/LoadingIndicator";

  export default {
    name: 'BetSlip',
    components: {LoadingIndicator, BetForm},
    data () {
      return {
        stake: 50,
        accountType: '',
        potentialWin: '',
        bets: [],
        isOpen: true,
        responseData: [],
        betSubmitted: false,
        isGroup: false,
        uuid: '',
        dareFriend: false,
        opponentPhone: '',
        groups: [],
        gamesInBetSlip: [],
        anSlip: {},
        showAnSlip: false,
        isLoading: false
      }
    },
    created () {
      this.getBetSlip();
    },
    watch: {
      'betSlip' (val, oldVal) {
        this.betSlip.forEach((bet) => {
          if (bet.game && GAME.isStarted(bet.game)) {
            this.$noty.error('The game ' + bet.game.home_team + 'VS' + bet.game.away_team + ' is already started, Please review your bet.')
            this.$store.dispatch('removeFromSlip', bet)
          }
        });
        if (val.length >= oldVal.length && val[val.length-1]) {
          this.animateAddedSlip(val[val.length-1].oddId);
        }
      },
      'authUser' () {
        if (this.authUser === null) {
          this.isGroup = false
        }
      },
      'isGroup' () {
        if (this.isGroup) {
          this.dareFriend = false
        }
      },
      'dareFriend' () {
        if (this.dareFriend) {
          this.isGroup = false
        }
      }
    },
    computed: {
      ...mapGetters(['betSlip', 'authUser', 'userDetails', 'userGroups'])
    },

    methods: {
      ...mapActions(['getBetSlip', 'getUserDetails']),
      removeFromBetSlip (bet) {
        console.log('removing it from the bet slip', bet)
        this.$store.dispatch('removeFromSlip', bet)
      },

      accountSelectedIsSufficient(account) {
        switch (account) {
            case ('main'):
                return this.authUser.account.main >= 25
            case ('bonus'):
                return this.authUser.account.bonus >= 25
            case ('sambaza'):
                return this.authUser.account.sambaza >= 25
            default:
                return false
        }
      },
      mainAccountIsSufficient () {
          if (!this.authUser && !this.accountType) return
          return this.accountType === 'main' && this.authUser.account.main < 25
      },
      bonusAccountIsSufficient () {
          if (!this.authUser && !this.accountType) return
          return this.accountType === 'bonus' &&  this.authUser.account.bonus < 25
      },
      sambazaAccountIsSufficient () {
          if (!this.authUser && !this.accountType) return
          return this.accountType === 'sambaza' &&  this.authUser.account.sambaza < 25
      },
      submitSuggestions () {
        let picks = []
        let reviewBetSlip = false
        this.betSlip.forEach((bet) => {
          if (GAME.isStarted(bet.game)) {
            this.$noty.error('The game ' + bet.game.home_team + 'vs' + bet.game.away_team + ' is already started, Please review your bet slip')
            this.$store.dispatch('removeFromSlip', bet)
            reviewBetSlip = true
          } else {
            picks.push({
              game_id: parseInt(bet.gameId),
              home_team: bet.game.home_team,
              away_team: bet.game.away_team,
              jackpot: bet.game.is_jackpot,
              odd: bet.oddValue,
              id: bet.oddId,
              pick: bet.oddOutCome,
              scheduled: bet.scheduled,
              market_id: bet.market_id
            })
          }
        })
        if (reviewBetSlip) { return }
        let postData = {
          picks: picks,
          is_group: this.isGroup,
          group_id: this.uuid
        }
        axios.post(GROUP_SUGGEST_URL, postData, {headers: AUTH_CONFIG.getHeaders()}).then((resp) => {
          let suggestionsResponseData = resp.data
          this.$noty.success('Suggestions sent successfully')
          this.$store.dispatch('init_auth_user', {request: 'refresh'})
          this.$store.dispatch('clearBetSlip')
          console.log('Submitted suggestions', suggestionsResponseData)
          this.isGroup = false
        }, () => {
            this.$noty.error(this.$Exception.showErrorMessage())
        })
      },

      getTotalOdds () {
        let odds = this.betSlip
        let totalOdds = Number(1)
        odds.forEach((odd) => {
          totalOdds = totalOdds * odd.oddValue
        })
        return totalOdds ? totalOdds.toFixed(2) : null
      },
      hideSlip () {
        this.isOpen = !this.isOpen
      },
      possibleWin () {
        let possibleWin = this.getTotalOdds() * this.stake
          possibleWin = possibleWin > 1000000 ? 1000000 : possibleWin
        return possibleWin ? possibleWin.toFixed(2) : null
      },
      submitDare () {
        let bet = this.betSlip[0]
        let pick = {
          game_id: parseInt(bet.gameId),
          home_team: bet.game.home_team,
          away_team: bet.game.away_team,
          odd: bet.oddValue,
          id: bet.oddId,
          pick: bet.oddOutCome,
          scheduled: moment(bet.scheduled).format('YYYY-MM-DD HH:mm:ss'),
          market_id: bet.market_id
        }
        let postData = {
          stake: this.stake,
          picks: pick,
          opponent: this.opponentPhone
        }
        axios.post(MAN_TO_MAN, postData, {headers: AUTH_CONFIG.getHeaders()}).then((resp) => {
          let responseData = resp.data
          console.log('Submit Dare Payload', resp.data)
          if (responseData.code === '200') {
            this.$noty.success(responseData.message)
            this.$store.dispatch('init_auth_user', {request: 'refresh'})
            this.$store.dispatch('clearBetSlip')
          }
        }, () =>  this.$noty.error(this.$Exception.showErrorMessage()))
      },
      submitBetSlip () {
        this.isLoading = true
        this.betSubmitted = true
        let reviewBetSlip = false
        let pickedGames = []
        this.betSlip.forEach((bet) => {
          if (GAME.isStarted(bet.game)) {
            this.$noty.error('The game ' + bet.game.home_team + 'vs' + bet.game.away_team + ' is already started, Please review your bet slip')
            this.$store.dispatch('removeFromSlip', bet)
            reviewBetSlip = true
          } else {
            pickedGames.push({
              game_id: parseInt(bet.gameId),
              home_team: bet.game.home_team,
              away_team: bet.game.away_team,
              jackpot: bet.game.is_jackpot,
              odd: bet.oddValue,
              id: bet.oddId,
              pick: bet.oddOutCome,
              scheduled: bet.scheduled,
              market_id: bet.market_id
            })
          }
        })
        if (reviewBetSlip) { return }
        console.log('picked', pickedGames)
        let postData = {
          user_id: this.authUser.owner_id,
          jackpot: false,
          amount: this.stake,
          account: this.accountType,
          picks: pickedGames,
          is_group: this.isGroup
        }
        this.responseData = []
        axios.post(PLACE_BET_URL, postData, {headers: AUTH_CONFIG.getHeaders()}).then((response) => {
          let responseData = response.data
          console.log(responseData)
          if (responseData.code === 200) {
              this.isLoading = false
            this.$noty.success(responseData.message)
            this.$store.dispatch('clearBetSlip')
            this.betSubmitted = false
            this.$store.dispatch('init_auth_user', {request: 'refresh'})
          } else {
            this.responseData.push(responseData.message)
            this.$noty.error(responseData.message)
            this.betSubmitted = false
            this.isLoading = false
          }
        }, () => {
            this.$noty.error(this.$Exception.showErrorMessage())
            this.isLoading = false
        })
      },
      animateAddedSlip (id) {
        let slipEl = document.querySelectorAll(`.slip${id}`);
        const slipBox = this.isOpen ? this.$refs.betSlipBtn : this.$refs.betSlipBox;

        if (slipEl.length) {
          if (slipEl.length > 1) {
            slipEl = [].filter.call(slipEl, (item) => {
              return item.getBoundingClientRect().width > 0
            })[0];
          } else {
            slipEl = slipEl[0];
          }
          this.anSlip = {
            width: `${slipEl.offsetWidth}px`,
            height: `${slipEl.offsetHeight}px`,
            top: `${slipEl.getBoundingClientRect().top}px`,
            left: `${slipEl.getBoundingClientRect().left}px`
          };
          this.showAnSlip = true;
          setTimeout(() => {
            if (window.innerWidth < 768) {
              this.anSlip = {
                ...this.anSlip,
                width: '100vw',
                top: '100vh',
                left: '0px'
              };
            } else {
              this.anSlip = {
                ...this.anSlip,
                width: '20px',
                top: `${slipBox ? slipBox.getBoundingClientRect().top : 200}px`,
                left: slipBox ? `${slipBox.getBoundingClientRect().left}px` : '100vw'
              };
            }
          }, 1);
          setTimeout(() => {
            this.showAnSlip = false;
            this.anSlip = {};
          }, 700);
        }
      }
    }
  }
</script>

